@charset "UTF-8";
*, *:before, *:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

@font-face {
  font-family: "Navigo";
  src: url("../fonts/Navigo-Bold.woff2") format("woff2"), url("../fonts/Navigo-Bold.woff") format("woff"), url("../fonts/Navigo-Bold.svg#Navigo-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ALS Hauss";
  src: url("../fonts/ALSHauss-Medium.woff2") format("woff2"), url("../fonts/ALSHauss-Medium.woff") format("woff"), url("../fonts/ALSHauss-Medium.svg#ALSHauss-Medium") format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ALS Hauss";
  src: url("../fonts/ALSHauss-Regular.woff2") format("woff2"), url("../fonts/ALSHauss-Regular.woff") format("woff"), url("../fonts/ALSHauss-Regular.svg#ALSHauss-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Futura PT Demi";
  src: url("../fonts/FuturaPT-Demi.woff2") format("woff2"), url("../fonts/FuturaPT-Demi.woff") format("woff"), url("../fonts/FuturaPT-Demi.svg#FuturaPT-Demi") format("svg");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
body {
  line-height: 100%;
  font-family: "Navigo", sans-serif;
  font-family: "ALS Hauss", sans-serif;
  color: #2b2d33;
}

.container {
  width: 100%;
  max-width: 1136px;
  padding: 0px 32px;
  margin: 0 auto;
}

p {
  color: #808185;
  font-size: 16px;
  line-height: 26px;
}

.main__title {
  font-size: 54px;
  font-family: "Navigo", sans-serif;
  font-weight: 700;
  line-height: 70px;
  color: #fff;
}

.title {
  font-size: 48px;
  font-family: "Navigo", sans-serif;
  font-weight: 700;
  line-height: 54px;
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 56px 0px;
  row-gap: 32px;
}

.block {
  flex-basis: calc(50% - 16px);
  border-radius: 12px;
  background: #f8f8f8;
  padding: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
  overflow: hidden;
}

.block__title {
  font-size: 18px;
  font-weight: 550;
  line-height: 28px;
  margin-bottom: 16px;
}

.block__text {
  line-height: 22px;
  font-size: 14px;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 13px 24px;
  border-radius: 8px;
  background: #fee600;
  font-style: normal;
  font-weight: 550;
  line-height: 26px;
  color: #2b2d33;
  text-decoration: none;
}

.accordion {
  border-radius: 12px;
  border: 1px solid #e9eaea;
  width: 99%;
  overflow: hidden;
}

.accordion__title {
  color: #2b2d33;
  display: flex;
  align-items: center;
  padding: 24px 32px;
  font-size: 16px;
  font-weight: 550;
  line-height: 26px;
  cursor: pointer;
  position: relative;
  transition: background 0.3s;
}

.accordion__title img {
  margin-right: 26px;
}

.accordion__title::before {
  content: "";
  position: absolute;
  right: 24px;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background: url(../img/icons/select-arrow-icon.svg) no-repeat center;
  transition: all 0.3s;
}

.accordion__title.active {
  background: #f8f8f8;
  border-bottom: 1px solid #e9eaea;
}

.accordion__title.active::before {
  transform: translateY(-50%) rotate(180deg);
}

.accordion__subtitle {
  color: #2b2d33;
  font-size: 18px;
  font-weight: 550;
  line-height: 28px;
  margin-bottom: 32px;
}

.accordion__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 48px;
  margin-bottom: 48px;
}

.accordion__wrapper:last-child {
  margin-bottom: 0;
}

.accordion__content {
  padding: 0px 32px;
  max-height: 0px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all 0.4s;
}

.accordion__content.show {
  padding-top: 32px;
  padding-bottom: 32px;
}

.icon__wrapper {
  padding: 12px;
  border-radius: 12px;
  background-color: #fee600;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.file {
  display: flex;
  align-items: flex-start;
  gap: 24px;
  text-decoration: none;
}

.file__title {
  font-weight: 550;
  font-size: 16px;
  line-height: 26px;
  color: #2b2d33;
}

.file__title a {
  color: #00809c;
  text-decoration: none;
}

.file__text {
  margin-top: 4px;
  font-size: 14px;
  line-height: 22px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 24px;
}

.file__text span {
  color: #a2a4a6;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 10;
}

.header .container {
  transition: all 0.2s;
}

.header .container.active {
  width: 695px;
}

.header__top {
  padding: 20px 0px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e9eaea;
  position: relative;
}

.header__bottom {
  padding: 10px 0px;
}

.header-logo {
  display: flex;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 32px;
  color: #2b2d33;
  font-family: "Futura PT Demi", sans-serif;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  line-height: 18px;
  width: 40px;
  overflow: hidden;
}

.header-logo span {
  margin-left: 12px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  width: 170px;
}

.header-logo span.active {
  opacity: 1;
  visibility: visible;
  pointer-events: none;
}

.header-nav {
  display: flex;
  align-items: center;
}

.header-nav__link {
  font-size: 16px;
  font-weight: 550;
  line-height: 26px;
  color: #808185;
  margin-right: 32px;
  text-decoration: none;
  transition: all 0.3s;
}

.header-nav__link:last-child {
  margin-right: 0;
}

.header-nav__link.active {
  color: #2b2d33;
}

.header-nav__link:hover {
  color: #2b2d33;
}

.header__email {
  font-size: 16px;
  color: #2b2d33;
  font-weight: 550;
  line-height: 26px;
  margin-left: auto;
  text-decoration: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 32px;
}

.header-submenu {
  display: flex;
  align-items: center;
}

.header-submenu__link {
  margin-right: 32px;
  color: #808185;
  text-decoration: none;
  font-size: 14px;
  line-height: 22px;
  transition: all 0.3s;
}

.header-submenu__link:hover {
  color: #2b2d33;
}

.header__hamburger {
  display: none;
  position: relative;
  z-index: 11;
  width: 20px;
  height: 16px;
  cursor: pointer;
  flex-shrink: 0;
  margin-left: 20px;
}

.header__hamburger::before, .header__hamburger::after, .header__hamburger span {
  left: 0;
  position: absolute;
  height: 2px;
  width: 100%;
  transition: all 0.3s;
  background-color: #2b2d33;
}

.header__hamburger::before, .header__hamburger::after {
  content: "";
}

.header__hamburger::before {
  top: 0;
}

.header__hamburger::after {
  bottom: 0;
}

.header__hamburger span {
  top: 50%;
  transform: scale(1) translate(0, -50%);
}

.header__hamburger.active::after, .header__hamburger.active::before, .header__hamburger.active span {
  background: #2b2d33;
}

.header__hamburger.active span {
  display: none;
}

.header__hamburger.active::before {
  top: 50%;
  transform: translate(0px, -50%) rotate(-45deg);
}

.header__hamburger.active::after {
  bottom: 50%;
  transform: translate(0px, 50%) rotate(45deg);
}

.lock {
