  }
}
@media (max-width: 1200px) {
  .header .container {
    padding-left: 100px;
  }
}
@media (max-width: 992px) {
  .container {
    padding: 0px 16px;
  }
  .title {
    font-size: 36px;
    line-height: 120%;
  }
  .header__hamburger {
    display: block;
  }
  .header__top {
    padding: 16px;
  }
  .header__top .container {
    padding: 0;
    height: 100%;
    width: 100vw !important;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    left: 0;
    top: 72px;
    z-index: 10;
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s;
  }
  .header__top .container.show {
    opacity: 1;
    visibility: visible;
  }
  .header-nav {
    width: 100%;
    padding-bottom: 40px;
    padding-top: 20px;
    background: #fff;
    flex-direction: column;
    align-items: flex-start;
  }
  .header-nav__link {
    padding: 12px 16px;
  }
  .header-logo {
    position: static;
    transform: none;
    flex-shrink: 0;
  }
  .header-logo span {
    display: none;
  }
  .header__email {
    position: static;
    transform: none;
  }
  .header-submenu {
    overflow: auto;
  }
  .header-submenu::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }
  .header-submenu__link {
    flex-shrink: 0;
  }
  .header__bottom .container {
    width: 100%;
    padding: 0px 16px;
    padding-right: 0;
  }
  .main.disclosure {
    padding-top: 67px;
  }
  .main.disclosure .primary {
    background-position: right -70px bottom;
    background-size: 60vw;
  }
  .fundManagers__wrapper {
    flex-direction: column;
  }
  .insurance__wrapper {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .main__title {
    font-size: 7vw;
    line-height: 130%;
  }
  .main.documents .primary {
    background-position: right -100px bottom;
  }
  .feature__wrapper {
    flex-direction: column;
  }
  .customSolutions__wrapper {
    flex-direction: column;
  }
  .outsourcing__wrapper {
    flex-direction: column;
  }
  .values__wrapper {
    flex-direction: column;
  }
  .raiffeisen-banner {
    padding: 20px;
  }
}
@media (max-width: 576px) {
  .title {
    font-size: 26px;
