@font-face {
    font-family: 'Navigo';
    src: url('../fonts/Navigo-Bold.woff2') format('woff2'),
        url('../fonts/Navigo-Bold.woff') format('woff'),
        url('../fonts/Navigo-Bold.svg#Navigo-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

